@font-face {
  font-weight: normal;
  font-family: "Blogger Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "Blogger Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
}
@font-face {
  font-weight: 300;
  font-family: "Blogger Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "Blogger Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-size: 16px;
  line-height: 22px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  color: #ffffff;
  font-style: normal;
  background-color: #182044;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-page--gray {
  background-color: #edeff6;
}
.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    margin: 0 auto;
    padding: 0 40px;
  }
  .container__inner {
    width: 618px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    margin: 0 auto;
    padding: 0 60px;
  }
  .container__inner {
    width: 1180px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: baseline;
  }
  .container--buddies {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
.button {
  padding-top: 24px;
  padding-left: 24px;
  padding-bottom: 24px;
  font-size: 18px;
  line-height: 19px;
  font-weight: 700;
  text-decoration: none;
  color: #1d2e5b;
  background-color: #ffd74b;
  border-radius: 10px;
  background-image: url("../img/triangle-arrow-right.svg");
  background-repeat: no-repeat;
  background-position: top 52% right 20px;
  background-size: 10px 14px;
}
.button:hover {
  background-color: #ffbd30;
}
.button:active {
  color: rgba(29, 46, 91, 0.3);
  background-image: url("../img/triangle-arrow-right-opacity.svg");
}
.button--outline {
  padding: 18px 20px;
  border: 3px solid #192144;
}
.button--rounded {
  padding: 5px 25px;
  color: #161c35;
  text-align: center;
  text-transform: uppercase;
  border-radius: 25px;
  background-image: none;
}
.button--outline:hover {
  color: #ffffff;
  background-color: #192144;
  background-image: url("../img/triangle-arrow-right-white.svg");
}
.button--outline:active {
  color: rgba(255, 255, 255, 0.3);
  background-image: url("../img/triangle-arrow-right-white-opacity.svg");
}
@media (min-width: 768px) {
  .button {
    font-size: 24px;
    line-height: 24px;
    border-radius: 20px;
    background-position: top 52% right 35px;
  }
}
.header {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: #161c36;
}
.header__container {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.header-wrapper {
  display: none;
  align-items: center;
}
.header__return {
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}
.header__logo__img {
  display: block;
}
.header__logo {
  display: block;
  width: 100px;
  height: 19px;
  margin-top: 10px;
}
.header__logo--blue {
  display: none;
}
.header__menu-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  z-index: 2;
  font-size: 0;
  display: block;
  width: 40px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.header__menu-btn::before {
  content: "";
  position: absolute;
  top: 6px;
  right: 10px;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff;
  border-radius: 2px;
}
.header--menu-open {
  background-color: #ffffff;
}
.header--menu-open .header__logo--blue {
  display: block;
}
.header--menu-open .header__logo--white {
  display: none;
}
.header--menu-open .header__menu-btn {
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 2;
  font-size: 0;
  display: block;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.header .header__menu-btn--hidden {
  display: none;
}
.header--menu-open .header__menu-btn::before,
.header--menu-open .header__menu-btn::after {
  content: "";
  position: absolute;
  top: 13px;
  right: 8px;
  width: 25px;
  height: 2px;
  background-color: #cccccc;
  box-shadow: none;
}
.header--menu-open .header__menu-btn::before {
  transform: rotate(45deg);
}
.header--menu-open .header__menu-btn::after {
  transform: rotate(-45deg);
}
.header__nav,
.header__login-btn,
.header__contacts,
.header__social-links {
  display: none;
}
.header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-bottom: 40px;
}
.header--menu-open .header__nav,
.header--menu-open .header__contacts,
.header--menu-open .header__social-links {
  display: flex;
}
.header--menu-open .header__social-links,
.header--menu-open .header__nav {
  margin: 0 auto;
}
.header__contacts {
  flex-direction: column;
}
.header--menu-open .header__social-links {
  margin-bottom: 20px;
}
.header--menu-open .header__login-btn {
  display: block;
  width: 100%;
  padding: 16px;
  text-align: center;
  margin-bottom: 65px;
}
.header--menu-open .header__login-btn:hover {
  background-color: #ffbd30;
}
.header--menu-open .header__login-btn:active {
  color: rgba(22, 28, 53, 0.3);
  background-image: none;
}
.header--scroll {
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #ffffff;
}
.header--scroll .header__logo--white {
  display: none;
}
.header--scroll .header__logo--blue {
  display: block;
  margin-top: 0;
}
.header--scroll .header__menu-btn {
  top: -3px;
}
.header--scroll .header__menu-btn::before,
.header--scroll .header__menu-btn::after {
  background-color: #cccccc;
  box-shadow: 0 8px 0 0 #cccccc;
  border-radius: 2px;
}
@media (min-width: 768px) {
  .header {
    padding-top: 40px;
    background-color: #182044;
  }
  .header--dark {
    background-color: #161c35;
  }
  .header__container {
    align-items: center;
  }
  .header__login-btn {
    display: block;
    width: 190px;
    padding: 10px 26px;
    font-size: 20px;
    line-height: 30px;
    border-radius: 25px;
  }
  .header__login-btn:active {
    background-image: none;
  }
  .header__logo {
    width: 166px;
    height: 42px;
    margin-top: 0;
    background-image: url("../img/logo-tablet-white@1x.png");
  }
  .header__menu-btn,
  .header--menu-open .header__menu-btn {
    right: 47%;
    top: 16px;
  }
  .header--menu-open .header__contacts {
    padding-left: 60px;
    margin-top: 100px;
  }
  .header--menu-open {
    background-color: #ffffff;
  }
  .header--menu-open .header__social-links {
    width: 100%;
    padding-left: 60px;
  }
  .header--menu-open .header__logo {
    background-image: url("../img/logo-tablet-blue@1x.png");
    order: -3;
  }
  .header--menu-open .header__login-btn {
    display: inline-block;
    vertical-align: bottom;
    width: 190px;
    margin-bottom: 0;
    padding: 10px 30px;
    margin-left: auto;
    order: -1;
    border-radius: 25px;
  }
  .header--menu-open .header__menu-btn {
    order: -2;
  }
  .header--menu-open .header__nav {
    width: 100%;
  }
  .header-wrapper {
    display: flex;
    justify-content: space-between;
  }
  .header--scroll {
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #ffffff;
  }
  .header .header__menu-btn {
    top: 14px;
  }
}
@media (min-width: 1440px) {
  .header {
    margin: 0;
    padding: 0;
    background-color: #161c35;
  }
  .header__container {
    background-color: #161c35;
    padding-top: 35px;
  }
  .header__logo {
    background-image: url("../img/logo-desktop-white@1x.png");
    width: 200px;
    height: 52px;
  }
  .header__nav {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 85px;
    color: #ffffff;
    background-color: transparent;
  }
  .header__nav::before {
    content: "";
    position: absolute;
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    top: 50%;
    left: 40px;
    margin-top: -4px;
  }
  .header__menu-btn {
    display: none;
  }
  .header__contacts {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    order: 1;
  }
  .header__login-btn {
    order: 2;
  }
  .header__title {
    margin-top: 120px;
    margin-bottom: 60px;
  }
  .header--scroll,
  .header--scroll .header__container {
    background-color: #ffffff;
  }
  .header--scroll a {
    color: #1d2e5b;
  }
  .header--scroll .header__nav::before {
    background-color: #1d2e5b;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
.site-list__link {
  font-size: 30px;
  font-weight: 500;
  line-height: 36px;
  color: #192144;
  text-transform: uppercase;
  text-decoration: none;
}
.site-list__link:hover {
  color: #ffd74b;
}
.site-list__link:active {
  color: rgba(255, 215, 75, 0.3);
}
@media (min-width: 768px) {
  .site-list {
    align-items: flex-start;
    padding-left: 60px;
  }
  .site-list__link {
    font-size: 60px;
    line-height: 71px;
  }
}
@media (min-width: 1440px) {
  .site-list {
    flex-direction: row;
  }
  .site-list__link {
    display: inline-block;
    color: #ffffff;
    font-size: 20px;
    font-weight: 500;
    margin-right: 35px;
    overflow: hidden;
  }
  .site-list__hover {
    display: inline-block;
    position: relative;
    transition: transform 0.3s;
  }
  .site-list__hover::before {
    content: "";
    position: absolute;
    display: block;
    height: 20px;
    top: 100%;
  }
  .site-list__hover--about::before {
    content: " ";
  }
  .site-list__hover--destinations::before {
    content: "";
  }
  .site-list__hover--buddies::before {
    content: "";
  }
  .site-list__link:hover span {
    transform: translateY(-100%);
  }
}
.user-block {
